<template>
  <div class="request-form-container">
    <h2>新建资源申请</h2>
    <form class="request-form" @submit.prevent="handleSubmitRequest">
      <!-- 基本信息 -->
      <div class="form-section">
        <h3>基本信息</h3>
        <div class="form-row">
          <div class="form-group">
            <label for="resourceType">资源类型 <span class="required">*</span></label>
            <select v-model="formData.resourceType" id="resourceType" class="form-select">
              <option value="">请选择资源类型</option>
              <option value="server">服务器</option>
              <option value="gpu">显卡</option>
              <option value="storage">存储资源</option>
              <option value="network">网络资源</option>
              <option value="software">软件许可证</option>
              <option value="other">其他资源</option>
            </select>
          </div>
          <div class="form-group">
            <label for="requestTitle">申请标题 <span class="required">*</span></label>
            <input v-model="formData.requestTitle" type="text" id="requestTitle" class="form-input" placeholder="请输入申请标题">
          </div>
          <div class="form-group">
            <label for="urgencyLevel">紧急程度</label>
            <select v-model="formData.urgencyLevel" id="urgencyLevel" class="form-select">
              <option value="normal">一般</option>
              <option value="urgent">紧急</option>
              <option value="veryUrgent">非常紧急</option>
            </select>
          </div>
          <div class="form-group">
            <label for="expectedDate">期望完成日期</label>
            <input v-model="formData.expectedDate" type="date" id="expectedDate" class="form-input">
          </div>
        </div>
      </div>

      <!-- 资源需求详情 -->
      <div class="form-section">
        <h3>资源需求详情</h3>
        <div class="form-row">
          <div class="form-group">
            <label for="serverId">选择服务器 <span class="required">*</span></label>
            <select v-model="formData.serverId" id="serverId" class="form-select" @change="onServerChange">
              <option value="">请选择服务器</option>
              <option v-for="server in availableServers" :key="server.id" :value="server.id">
                {{ server.name }} ({{ server.ip }}) - CPU: {{ server.cpuUsage }}%, Memory: {{ server.memoryUsage }}%
              </option>
            </select>
          </div>
          <div class="form-group">
            <label for="gpuIndex">起始显卡编号 <span class="required">*</span></label>
            <input v-model="formData.gpuIndex" type="number" id="gpuIndex" class="form-input" placeholder="从1开始" min="1" :max="availableGpuCount">
          </div>
          <div class="form-group">
            <label for="gpuCount">显卡数量 <span class="required">*</span></label>
            <input v-model="formData.gpuCount" type="number" id="gpuCount" class="form-input" placeholder="至少1张" min="1" :max="availableGpuCount">
          </div>
        </div>
        <div class="form-row">
          <div class="form-group full-width">
            <label for="resourceDescription">资源需求描述</label>
            <textarea v-model="formData.resourceDescription" id="resourceDescription" class="form-textarea" placeholder="请详细描述您的资源需求和用途..."></textarea>
          </div>
        </div>
      </div>

      <!-- 评分体系 -->
      <div class="form-section">
        <h3>评分体系</h3>
        <div class="form-row">
          <div class="form-group">
            <label for="projectContributionScore">项目贡献度 (1-10) <span class="required">*</span></label>
            <input v-model="formData.projectContributionScore" type="number" id="projectContributionScore" class="form-input" min="1" max="10">
          </div>
          <div class="form-group">
            <label for="researchAchievementScore">科研成果 (1-10) <span class="required">*</span></label>
            <input v-model="formData.researchAchievementScore" type="number" id="researchAchievementScore" class="form-input" min="1" max="10">
          </div>
          <div class="form-group">
            <label for="resourceUsageEfficiencyScore">资源使用效率 (1-10) <span class="required">*</span></label>
            <input v-model="formData.resourceUsageEfficiencyScore" type="number" id="resourceUsageEfficiencyScore" class="form-input" min="1" max="10">
          </div>
          <div class="form-group">
            <label for="fairnessScore">公平性考量 (1-10) <span class="required">*</span></label>
            <input v-model="formData.fairnessScore" type="number" id="fairnessScore" class="form-input" min="1" max="10">
          </div>
        </div>
        <div v-if="comprehensiveScore > 0" class="form-row">
          <div class="form-group">
            <label>综合评分</label>
            <div class="score-display">
              {{ comprehensiveScore.toFixed(1) }}/10
            </div>
          </div>
          <div class="form-group">
            <label>资源匹配度</label>
            <div class="score-display">
              {{ basicMatchScore.toFixed(1) }}/10
            </div>
          </div>
        </div>
      </div>

      <!-- 联系方式 -->
      <div class="form-section">
        <h3>联系方式</h3>
        <div class="form-row">
          <div class="form-group">
            <label for="contactPerson">联系人 <span class="required">*</span></label>
            <input v-model="formData.contactPerson" type="text" id="contactPerson" class="form-input" placeholder="请输入联系人姓名">
          </div>
          <div class="form-group">
            <label for="contactPhone">联系电话 <span class="required">*</span></label>
            <input v-model="formData.contactPhone" type="tel" id="contactPhone" class="form-input" placeholder="请输入联系电话">
          </div>
          <div class="form-group">
            <label for="contactEmail">电子邮箱 <span class="required">*</span></label>
            <input v-model="formData.contactEmail" type="email" id="contactEmail" class="form-input" placeholder="请输入电子邮箱">
          </div>
        </div>
      </div>

      <!-- 表单操作 -->
      <div class="form-actions">
        <button type="button" class="cancel-button" @click="resetForm">重置</button>
        <button type="submit" class="submit-button" :disabled="isSubmitting">
          {{ isSubmitting ? '提交中...' : '提交申请' }}
        </button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'ResourceRequestForm',
  props: {
    availableServers: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      isSubmitting: false,
      formData: {
        resourceType: '',
        requestTitle: '',
        urgencyLevel: 'normal',
        expectedDate: '',
        cpuCount: '',
        memorySize: '',
        storageSize: '',
        // GPU相关需求
        serverId: '',
        gpuIndex: '',
        gpuCount: 1,
        resourceDescription: '',
        // 评分体系
        projectContributionScore: '',
        researchAchievementScore: '',
        resourceUsageEfficiencyScore: '',
        fairnessScore: '',
        contactPerson: '',
        contactPhone: '',
        contactEmail: ''
      }
    }
  },
  methods: {
    // 提交申请
    async handleSubmitRequest() {
      try {
        // 表单验证
        if (!this.validateForm()) {
          return
        }
        
        this.isSubmitting = true
        // 模拟API请求
        await new Promise(resolve => setTimeout(resolve, 1500))
        
        // 计算综合评分
        const projectContributionScore = parseInt(this.formData.projectContributionScore)
        const researchAchievementScore = parseInt(this.formData.researchAchievementScore)
        const resourceUsageEfficiencyScore = parseInt(this.formData.resourceUsageEfficiencyScore)
        
        // 计算综合得分：项目贡献度(40%) + 科研成果(30%) + 资源使用效率(30%)
        const totalScore = projectContributionScore * 0.4 + researchAchievementScore * 0.3 + resourceUsageEfficiencyScore * 0.3
        
        // 获取选择的服务器信息
        const server = this.selectedServer
        
        // 模拟成功响应
        alert(`申请提交成功！\n申请ID：REQ${Date.now().toString().slice(-8)}\n您的申请评分为：${totalScore.toFixed(1)}\n申请GPU信息：\n- 服务器：${server.name} (${server.ip})\n- 显卡范围：#${this.formData.gpuIndex} 至 #${parseInt(this.formData.gpuIndex) + parseInt(this.formData.gpuCount) - 1}\n- 显卡数量：${this.formData.gpuCount}张`)
        this.resetForm()
      } catch (error) {
        console.error('提交申请失败：', error)
        alert('提交申请失败，请稍后重试')
      } finally {
        this.isSubmitting = false
      }
    },
    
    // 表单验证
    validateForm() {
      // 验证基本信息
      if (!this.formData.resourceType || !this.formData.requestTitle) {
        alert('请填写申请基本信息')
        return false
      }
      
      // 验证GPU相关信息
      if (!this.formData.serverId || !this.formData.gpuIndex || !this.formData.gpuCount) {
        alert('请填写完整的GPU需求信息')
        return false
      }
      
      // 验证评分（1-10分）
      const scores = [
        this.formData.projectContributionScore,
        this.formData.researchAchievementScore,
        this.formData.resourceUsageEfficiencyScore,
        this.formData.fairnessScore
      ]
      
      for (const score of scores) {
        const numScore = parseInt(score)
        if (isNaN(numScore) || numScore < 1 || numScore > 10) {
          alert('评分必须是1-10之间的数字')
          return false
        }
      }
    
      // 验证联系方式
      if (!this.formData.contactPerson || !this.formData.contactPhone || !this.formData.contactEmail) {
        alert('请填写完整的联系方式')
        return false
      }
      
      return true
    },
    
    // 重置表单
    resetForm() {
      this.formData = {
        resourceType: '',
        requestTitle: '',
        urgencyLevel: 'normal',
        expectedDate: '',
        cpuCount: '',
        memorySize: '',
        storageSize: '',
        // GPU相关需求
        serverId: '',
        gpuIndex: '',
        gpuCount: 1,
        resourceDescription: '',
        // 评分体系
        projectContributionScore: '',
        researchAchievementScore: '',
        resourceUsageEfficiencyScore: '',
        fairnessScore: '',
        contactPerson: '',
        contactPhone: '',
        contactEmail: ''
      }
    },
      
    // 处理服务器选择变化
    onServerChange() {
      // 当服务器改变时，重置显卡选择
      this.formData.gpuIndex = ''
      this.formData.gpuCount = 1
    },
    
    // 计算资源匹配度
    calculateResourceMatch(requested, available) {
      return requested <= available ? 1 : available / requested
    }
  },
  computed: {
      // 获取当前选择的服务器
      selectedServer() {
        return this.availableServers.find(server => server.id === parseInt(this.formData.serverId))
      },
      
      // 计算综合得分
      comprehensiveScore() {
        const projectContributionScore = parseInt(this.formData.projectContributionScore)
        const researchAchievementScore = parseInt(this.formData.researchAchievementScore)
        const resourceUsageEfficiencyScore = parseInt(this.formData.resourceUsageEfficiencyScore)
        const fairnessScore = parseInt(this.formData.fairnessScore)
        
        // 检查所有评分是否都已填写且为有效数字
        if (isNaN(projectContributionScore) || isNaN(researchAchievementScore) || isNaN(resourceUsageEfficiencyScore) || isNaN(fairnessScore)) {
          return 0
        }
        
        // 计算综合得分：项目贡献度(35%) + 科研成果(25%) + 资源使用效率(25%) + 公平性考量(15%)
        return projectContributionScore * 0.35 + researchAchievementScore * 0.25 + 
               resourceUsageEfficiencyScore * 0.25 + fairnessScore * 0.15
      },
      
      // 计算基础匹配度
      basicMatchScore() {
        if (!this.selectedServer) return 0;
        
        // 根据申请需求与服务器配置的匹配程度计算
        // 由于formData中没有cpu需求，使用gpuCount代替进行简化计算
        const gpuMatch = this.calculateResourceMatch(
          parseInt(this.formData.gpuCount), 
          this.selectedServer.gpuCount
        );
        
        // 综合计算基础匹配度（0-10分）
        return gpuMatch * 10;
      },
      
      // 获取可用显卡数量
      availableGpuCount() {
        if (!this.selectedServer) {
          return 0
        }
        return this.selectedServer.availableGpus.length
      }
  },
  watch: {
    // 监听服务器选择变化，自动重置表单相关字段
    'formData.serverId': {
      handler() {
        this.onServerChange()
      }
    }
  }
}
</script>

