  created() {
    // 如果是管理员，默认显示全部申请
    if (this.userRole === 'admin') {
      this.activeTab = 'allRequests'
    }
  },
  
  methods: {
    // 切换标签
    switchTab(tabName) {
      this.activeTab = tabName
    },
    
    // 查看申请详情
    viewRequestDetail(requestId) {
      console.log('查看申请详情：', requestId)
      // 实际应用中，这里会跳转到申请详情页或打开详情弹窗
    },
    
    // 取消申请
    cancelRequest(requestId) {
      // 实际应用中，这里会调用取消申请的API
      const request = this.requestsList.find(item => item.id === requestId)
      if (request) {
        request.status = 'cancelled'
      }
    },
    
    // 管理员功能：分配服务器
    allocateServer(requestId) {
      // 查找请求
      const request = this.allRequestsList.find(item => item.id === requestId)
      if (!request) return
      
      // 查找可用的服务器（按照CPU和内存使用率低的优先）
      const availableServer = this.availableServers
        .sort((a, b) => (a.cpuUsage + a.memoryUsage) - (b.cpuUsage + b.memoryUsage))
        .find(server => server.availableGpus.length > 0)
      
      if (availableServer) {
        // 分配服务器
        request.allocatedServer = availableServer
        request.status = 'approved'
        alert(`已成功分配服务器: ${availableServer.name}`)
      } else {
        alert('当前无可用服务器资源，请稍后再试')
      }
    },
    
    // 管理员功能：拒绝申请
    rejectRequest(requestId) {
      const request = this.allRequestsList.find(item => item.id === requestId)
      if (request) {
        request.status = 'rejected'
      }
    }
  }
